@import url(https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap);
@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.6.3/css/font-awesome.min.css);

.topbar__title {
  font-size: 50px;
  margin: 0 auto;
}

.topbar__container {
  border: 0px solid aqua;
  display: flex;
  flex-direction: column; 
  margin: 40px; 
  border-radius: 8px;
}

.topbar__search {
  border: 1px solid #ccc;      
  display: flex;
  justify-content: space-between;
}

.topbar__button {
  padding: 15px;
}

.topbar__input {
  border: 0px solid firebrick;
  padding: 10px;
  border-radius: 5px;
  width: 100%;
}



img {
  max-height: 200px;
}

.list {
  border: 0px solid crimson;
}

.list ul {
  border: 0px solid violet;
  margin-top: 50px;  
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  list-style: none;
}

.list li {
  border: 0px solid magenta;
  margin: 20px;
  text-align: center;
  display: flex;
  flex-direction: column;
  font-size: 1.5em;
  width: 200px;
}

.card {
  border: 0px solid greenyellow;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 0px 10px 2px rgba(123,123,123,0.5);
}

.card__content {
  border: 0px solid tomato;  
  display: flex;
  flex-direction: column;
  margin: 20px 0;
}

.list li img {
  display: block;
  border: 0px solid black;
}

.card__footer {
  border: 0px solid gold;
  width: 100%;
}

.card__button {  
  background-color: #3C5878;
  color: white;
  padding: 15px;
  font-weight: bold;
  font-size: 15px;
  width: 100%;
  cursor: pointer;
}
.cart {
  border: 0px solid tomato;
  width: 25%;
  display: block;  
  background-color: #ddd;
}

.cart__container {
  border: 0px solid lightseagreen; 
  padding: 10px; 
}

.cart__title {
  padding: 20px;
  text-align: center;  
  color: #222;

}

.cart__list {
  list-style: none;
  padding: 0;
}

.cart__item {
  border: 0px solid black;
  padding: 10px;
  background-color: white;
  display: flex;
  justify-content: space-between;
}
.cart__total {
  border: 0px solid green;
  padding: 10px;
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
}

.cart__finish {
  padding: 10px;
  width: 100%;
  font-size: 1.0em;
  cursor: pointer;
  background: #222;
  color: white;
  font-weight: bold;
}
* { 
  margin: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
}

html {
  border: 0px solid tomato;
  height: 100vh;  
}

body {
  border: 0px solid green; 
  font-family: 'Patrick Hand', sans-serif;     
}

.container {  
  display: flex;
  align-items: flex-start;
  justify-content: space-between;  
}

.content {
  border: 0px solid royalblue;
  width: 100%;
}
